
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    accountInfo: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  emits: ["pre-step"],
  setup(props, { emit }) {
    function preStep() {
      emit("pre-step");
    }
    return {
      preStep,
    };
  },
});
